"use client"
import Image from "next/image"
import {motion} from 'framer-motion'



export default function SectionTwo() {

    return <div className="  flex space-x-4  backdrop-blur-lg backdrop-saturate-[100%] m-10 rounded-3xl  bg-cyan-300/10  border-2 border-white  bg-opacity-10 p-10">
            <div className="  flex space-x-4  backdrop-blur-lg backdrop-saturate-[100%] m-2 rounded-3xl   bg-cyan-100/10  border-2 border-white  bg-opacity-10 p-10 "> 
             <div className="flex flex-col space-x-3 p-10">
                <div>  Get insights into </div>
               
                <div> 
                {[
                 {
                            
                            title: "Analyze" },
                                  {
                           
                            title: "Prediction",
                                  },].map((feature, index) => (
                            <motion.div
                              key={index}
                                  initial={{ opacity: 0, y: 20 }}
                                  whileInView={{ opacity: 1, y: 0 }}
                                  transition={{ delay: index * 0.1 }}
                              
                              className="flex flex-col  bg-white bg-opacity-10 border border-opacity-20 border-[#ffffff] items-start p-4 md:p-6 bg-primary rounded-xl shadow-lg hover:shadow-2xl transition-all duration-300"
                            >
                              <div className="p-2 md:p-3 bg-purple-200 border-2 border-purple-400 rounded-full mb-3 md:mb-4">
                                   
                                
                              </div>
                              <h3 className="text-lg md:text-xl font-lora font-bold mb-2">{feature.title}</h3>
                                 </motion.div>
                          ))}
                          </div> 
             </div>
             <div className=" flex flex-col space-y-7 p-10 bg-white/95 backdrop-blur-md rounded-2xl">
              <div className="flex space-x-7">
                <div>
                    <Image alt="pic1" className="rounded-3xl" width={1200} height={1000}  src={'/pic1.jpg'}/>

                </div>
                <div>
                <Image alt="pic1" className="rounded-3xl" width={1000} height={800} src={'/pic2.jpg'}/>
                </div>
              </div >
              <div  className="flex space-x-7">
                <div>
                <Image alt="pic1" className="rounded-3xl " width={1000} height={800} src={'/pic3.jpg'}/>
                </div>
                <div>
                <Image alt="pic1" className="rounded-3xl" width={1200} height={1000} src={'/pic4.jpg'}/>
                </div>

              </div>
               
             </div>
    </div>
    </div>
}